import React from 'react';
import Card from './Card';


const CardList = ( {robots} ) => {
   
    return (
        <div>
            {
             robots.map((user, i) => { //utilizo el map para recorrer los robots, con un user y un i como parametros, siempre tiene q retornar algo
                return( 
                     <Card
                        key={i}
                        id={robots[i].id}
                        name={robots[i].name}
                        username={robots[i].username}
                        email={robots[i].email}/>
                       
                     ); //la i para que recorra 1x1
            })

            }
         </div>
    );
}
export default CardList;